<template>
  <div id="api-dosage">
    <div class="breadcrumb">
      <a href="javascript:;">API用量</a>
    </div>
    <div class="table-list">
      <el-table
        :data="apiDosage"
        border
        :row-class-name="tableRowClassName"
        style="width: 100%">
        <el-table-column
          prop="name"
          label="API名称"
          width="">
        </el-table-column>
        <el-table-column
          prop="used"
          label="已调用次数">
        </el-table-column>
        <el-table-column
          prop="address"
          label="操作">
          <template scope="scope">
            <a href="javascript:;" @click="dialogVisible = true" style="color: #4378e6;">立即咨询</a>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      :visible.sync="dialogVisible"
      size="tiny">
      <h3>商务合作</h3>
      <ul>
        <li class="clearfix">
          <div class="boxname">咨询时间:</div>
          <div class="boxvalue">9:00 - 18:00</div>
        </li>
        <li class="clearfix">
          <div class="boxname">电话:</div>
          <div class="boxvalue">0755 - 22223970</div>
        </li>
        <li class="clearfix">
          <div class="boxname">QQ:</div>
          <div class="boxvalue">2020601975</div>
        </li>
      </ul>
      <span slot="footer" class="dialog-footer">
        <!--<el-button @click="dialogVisible = false">取 消</el-button>-->
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <div class="model" v-show="loginState">
      <div class="content">
        <a href="javascript:;">
          <b>!</b>
          登陆过期，请重新登陆。
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'

  export default {
    data() {
      return {
        apiHost: "",
        dialogVisible: false,
        apiDosage: [],
        loginState: false
      }
    },
    created() {
      var self = this;
      if(location.host != "robot.giiso.com") {
//        self.apiHost = "http://172.16.33.4:8081"
      }
      self.getApiDosage();
      $(".el-dialog__close").hide();
    },
    mounted() {
      $(".el-dialog__close").hide();
    },
    methods: {
      tableRowClassName(row, index) {
        if (index % 2 === 1) {
          return 'info-row';
        }
        return '';
      },
      //获取api用量信息列表
      getApiDosage() {
        var self = this;
        $.ajax({
          url: self.apiHost + "/open-portal/api/use",
          type: "POST",
          xhrFields: {
            withCredentials: true
          },
          crossDomain: true,
          data: {},
          success: function (msg) {
            if (msg.data && msg.data.length > 0) {
              self.apiDosage = msg.data;
            }
          },
          error: function (XMLHttpRequest) {
            if (XMLHttpRequest.status == "9001") {
              self.loginState = true;
              setTimeout(function () {
                location.href = "/";
                self.loginState = false;
              }, 2000)
            }
          }
        })
      },
    },
  }
</script>

<style lang="less" type="text/less">
  #api-dosage {
    .model {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.3);
      z-index: 99999999;
      .content {
        width: 260px;
        height: 60px;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -30px;
        margin-left: -130px;
        background-color: #fff;
        border-radius: 4px;
        a {
          display: block;
          text-align: center;
          line-height: 20px;
          margin-top: 20px;
          b {
            display: inline-block;
            height: 20px;
            width: 20px;
            border-radius: 50%;
            color: #f44444;
            border: 1px solid #f44444;
            margin-right: 10px;
          }
        }
      }
    }
    .breadcrumb {
      margin: 52px 0 21px 0;
      a {
        display: block;
        line-height: 30px;
        color: #141414;
        font-size: 24px;
      }
    }
    .table-list {
      .el-table td, .el-table th {
        height: 50px;
        text-align: center;
      }
      .el-table th {
        background: #F4F6FA;
        box-shadow: 0 1px 0 0 #D9D9D9;
      }
      .el-table th .cell {
        font-size: 14px;
        color: #111;
        font-weight: bold;
        background: #F4F6FA;
      }
      .el-table td .cell {
        font-size: 14px;
        color: #302525;
      }
      .el-table .info-row {
        background: #F4F6FA;
        box-shadow: 0 1px 0 0 #D9D9D9;
      }
    }
    .el-dialog {
      width: 400px;
      .el-dialog__header {
        padding: 10px 20px 10px 20px;
      }
      .el-dialog__body {
        padding-top: 0;
        h3 {
          font-size: 18px;
          color: #111;
          line-height: 25px;
          margin-bottom: 10px;
          margin-top: 20px;
        }
        ul {
          width: 195px;
          li {
            line-height: 20px;
            font-size: 14px;
            color: #555;
            .boxname {
              float: left;
              text-align: right;
              width: 70px;
            }
            .boxvalue {
              float: right;
              text-align: left;
              width: 113px;
            }
          }
        }
      }
      .el-dialog__footer {
        padding-top: 17px;
        padding-bottom: 17px;
        background-color: #f3f5f6;
        .el-button {
          width: 80px;
          background: #477EF8;
          border-radius: 2px;
        }
      }
    }
  }
</style>



